<template>
	<view class="container">
		<view class="express_box">
			<view class="rankTitle">业绩排名</view>
			<view class="rankBox rank_wrap">
				<view class="rankHead">
					<image :src="isHeader ? isHeader : headerImg" mode=""></image>
					<view class="rankLeft">
						<text>{{rank.salesmanName}}</text>
						<view v-if="tabIndex!=1"><text>成交：</text> <text class="green">{{rank.amount}}</text> {{tabIndex!=0?'万元':'套'}}</view>
						<view v-else>
							<text>成交：</text>
							<text class="green">{{rank.amount}}</text>万元 / 
							<text class="green" style="padding-left: 4rpx;">{{rank.count}}</text>套
						</view>
					</view>
				</view>
				<view class="rankRight">
					<view>目前排名</view>
					<view class="rankNum">{{rank.rank}}</view>
				</view>
			</view>
		</view>
		<view class="express_box" v-if="signList.length>0">
			<view class="rankTitle">{{tabIndex==0?'认购列表':(tabIndex==1?'签约列表':'回款列表')}}</view>
			<view class="rankBox achBox" v-for="(item,index) in signList" :key="index">
				<view class="flexSing"><text>房间：</text><text>{{item.roomCode}}</text></view>
				<view class="flexSing"><text>面积：</text><text>{{item.prebuildArea}}m²</text></view>
				<view class="flexSing"><text>客户：</text><text>{{item.customerName}}</text></view>
				<template v-if="tabIndex==2">
					<view class="flexSing"><text>合同总价：</text><text>{{item.money}}万</text></view>
					<view class="flexSing"><text>合同签订时间：</text><text>{{item.subscribleDate}}</text></view>
					<view class="flexSing"><text>已回款总金额：</text><text>{{item.payin}}万</text></view>
					<view class="flexSing"><text>当前应收款：</text><text>{{item.toBePaid}}万</text></view>
					<view class="flexSing"><text>合同状态：</text><text>{{item.contractState}}</text></view>
				</template>
				<template v-if="tabIndex!=2">
					<view class="flexSing"><text>{{tabIndex==0?'总房款':'签约金额'}}：</text><text>{{item.money}}万</text></view>
					<!-- <view class="flexSing"><text>置业顾问：</text><text>{{item.salesManName}}</text></view> -->
					<view class="flexSing" v-if="tabIndex!=2"><text>{{tabIndex==0?'认购时间':'签约时间'}}：</text><text>{{item.subscribleDate}}</text></view>
					<!-- <view class="flexSing" v-if="tabIndex==2"><text>最近回款时间：</text><text>{{item.lastRefundDate}}</text></view> -->
				</template>
			</view>
		</view>
	</view>
</template>

<script>
	import rankIcon from '@/static/moreIcon/rankIcon3.png';
	import header from '@/static/moreIcon/avatar.png';
	export default {
		props: ['tabIndex', 'rank', 'signList'],
		data() {
			return {
				headerImg: header, 
				rankImg: rankIcon,
				isHeader : uni.getStorageSync('loginInfo').headSculpture
			}
		}
	}
</script>

<style lang="scss">
	.express_box {
		background-color: #F6F6F6;
		padding: 0 30rpx;
		margin-top: 24rpx;
		.rankTitle{
			font-size: 36rpx;
			padding: 30rpx 20rpx;
			background: #fff;
			border-bottom: 1rpx solid #eee;
			border-radius: 16rpx;
		}
		.rank_wrap{
			align-items: center;
		}
		.rankBox {
			display: flex;
			justify-content: space-between;
			font-size: 30rpx;
			background: #fff;
			padding: 20rpx;
			border-radius: 16rpx;
			.rankHead{
				min-width: 80%;
				display: flex;
				align-items: center;
				image{
					min-width: 120rpx;
					width: 120rpx;
					height: 120rpx;
					border-radius: 50%;
					margin-right: 20rpx;
				}
				.rankLeft{
					view{
						font-size: 28rpx;
						padding-top: 12rpx;
						color: #666;
						.green{
							color: #03958C;
							padding-right: 4rpx;
						}
					}
				}
			}
			.rankRight{
				text-align: right;
				font-size: 24rpx;
				color: #999;
				.rankNum{
					font-size: 42rpx;
					color: #EA4918;
					margin-top: 12rpx;
					font-weight: bold;
				}
			}
		}
	
		.achBox {
			display: flex;
			flex-direction: column;
			border-bottom: 1rpx solid #eee;
			background: #fff;
			border-radius: 16rpx;
			padding: 24rpx;
			view {
				flex: 1;
				display: flex;
				padding: 10rpx;
	
				text {
					font-size: 28rpx;
					line-height: 40rpx;
				}
				text:first-child{
					width: 200rpx;
					display: inline-block;
				}
			}
		}
	}
</style>
